@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
:root {
  --body-bg: hsl(0, 0%, 100%);
  --body-topbg: hsl(225, 100%, 98%);
  --body-cardbg: hsl(227, 47%, 96%);
  --body-text: hsl(228, 12%, 44%);
  --body-darktext: hsl(230, 17%, 14%);
}

.dark-mode:root {
  --body-bg: hsl(230, 17%, 14%);
  --body-topbg: hsl(232, 19%, 15%);
  --body-cardbg: hsl(228, 28%, 20%);
  --body-text: hsl(228, 34%, 66%);
  --body-darktext: hsl(0, 0%, 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

.top_bg {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 35%;
  background-color: var(--body-topbg);
}

p {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: var(--body-text);
  font-weight: 700;
}

h1 {
  color: var(--body-darktext);
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--body-bg);
}

#conteiner {
  display: flex;
}

main {
  width: 1100px;
  height: 580px;
}

header {
  display: flex;
  margin-bottom: 40px;
}

#cabecalho {
  display: flex;
  width: 50%;
  flex-direction: column;
}

.toggle {
  display: flex;
  width: 50%;
  justify-content: end;
  align-items: center;
  gap: 10px;
}
.toggle p {
  font-size: 700;
}

.card {
  background-color: var(--body-cardbg);
  width: 250px;
  height: 200px;
  border-radius: 5px;
}
.card:hover {
  background-color: hsla(230, 22%, 74%, 0.363);
}

#conteiner {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.number {
  display: flex;
  justify-content: center;
  color: var(--body-darktext);
  font-size: 55px;
}

.card_number p {
  display: flex;
  justify-content: center;
}

.card_social {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 35px;
  height: 50px;
}

.social_up {
  color: hsl(163, 72%, 41%);
}

.social_today {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  height: 60px;
}

.social_down {
  color: hsl(356, 69%, 56%);
}

#card_linha1 {
  height: 4px;
  border-radius: 5px 5px 0px 0px;
  background-color: hsl(208, 92%, 53%);
}

#card_linha2 {
  height: 4px;
  border-radius: 5px 5px 0px 0px;
  background-color: hsl(203, 89%, 53%);
}

#card_linha3 {
  height: 4px;
  border-radius: 5px 5px 0px 0px;
  background: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
}

#card_linha4 {
  height: 4px;
  border-radius: 5px 5px 0px 0px;
  background-color: hsl(348, 97%, 39%);
}

.card_follow {
  letter-spacing: 5px;
  font-weight: 400;
  font-size: 14px;
}

#title_over {
  padding-top: 40px;
  padding-bottom: 20px;
  color: var(--body-text);
}

#conteiner2 {
  display: flex;
  justify-content: space-between;
  padding: 0px 0px 20px 0px;
}

#conteiner3 {
  display: flex;
  justify-content: space-between;
  padding: 0px 0px 20px 0px;
}

.card-second {
  background-color: var(--body-cardbg);
  width: 250px;
  height: 120px;
  border-radius: 5px;
}
.card-second:hover {
  background-color: hsla(230, 22%, 74%, 0.363);
}

.page_over {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50%;
  padding: 0px 25px 0px 25px;
}

.number_over {
  display: flex;
  justify-content: space-between;
  height: 50%;
  padding: 0px 25px 0px 25px;
}

.number2 {
  font-size: 30px;
  color: var(--body-darktext);
}

.esc_over {
  display: flex;
  align-items: center;
  gap: 5px;
}